* {
	font-family: 'SFDisplay', Arial, sans-serif; }

body {
	min-width: 320px;
	color: $dark-gray;
	font-family: 'SFDisplay', Arial, sans-serif;
	@include f(12px, 400, 1.3); }

.inner {
	position: relative;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	max-width: 700px;
	@include clr; }

.title-lg {
	@include f(32px, 700, 1.1875);
	@include sm {
		font-size: 32px*0.75; } }
