.btn {
	display: inline-block;
	vertical-align: top;
	padding: 9px 19px 6px;
	border: 1px solid $dark-sky-blue;
	border-radius: 4px;
	color: $dark-sky-blue;
	background: none;
	@include f(11px, 400, 13px);
	letter-spacing: 0.6px;
	text-align: center;
	text-transform: uppercase;
	transition: color 0.25s, background 0.25s;
	@include hover {
		color: $white;
		background: $blue; } }
