@mixin col($name) {
	&[data-column="#{$name}"] {
		@content; } }
.table {
	font-size: 0;
	&__thead {
		@include xs {
			display: none; } }
	&__tr {
		position: relative;
		padding-right: 35px;
		border-bottom: 1px solid #f2f2f2;
		@include xs {
			padding: 10px 35px 10px 0; } }
	&__th,
	&__td {
		display: inline-block;
		vertical-align: middle;
		padding: 0 5px;
		@include xs {
			padding: 0; }
		@include col("name") {
			width: 48%;
			@include xs {
				width: 100%; } }
		@include col("created") {
			width: 25.2%;
			@include xs {
				width: 100%; } }
		@include col("modified") {
			width: 25.8%;
			@include xs {
				width: 100%; } } }
	&__td {
		padding-top: 19px;
		padding-bottom: 19px;
		@include xs {
			padding-top: 6px;
			padding-bottom: 6px; }
		&[data-title] {
			&:before {
				@include xs {
					content: attr(data-title);
					display: block;
					margin-bottom: 6px;
					color: $cool-gray;
					@include f(12px, null, 1); } } } }
	&__title {
		color: $blue;
		@include f(16px, null, 1); }
	&__meta {
		@include f(14px, null, 1); }
	&__th {
		padding-bottom: 3px;
		color: $cool-gray;
		@include f(12px, null, 1); }
	&__btn {
		position: absolute;
		right: -14px;
		top: 50%;
		transform: translateY(-50%);
		@include xs {
			top: 0;
			@include gpu; } } }
