html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

input {
  appearance: none;
}

input::-ms-clear {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration: underline;
}

a:not([class]):hover {
  text-decoration: none;
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 400;
  src: local("SFDisplay Regular"), local("SFDisplay-Regular"), url("../fonts/sfuidisplay-regular.woff2") format("woff2"), url("../fonts/sfuidisplay-regular.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 500;
  src: local("SFDisplay Medium"), local("SFDisplay-Medium"), url("../fonts/sfuidisplay-medium.woff2") format("woff2"), url("../fonts/sfuidisplay-medium.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 600;
  src: local("SFDisplay Semibold"), local("SFDisplay-Semibold"), url("../fonts/sfuidisplay-semibold.woff2") format("woff2"), url("../fonts/sfuidisplay-semibold.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 700;
  src: local("SFDisplay Bold"), local("SFDisplay-Bold"), url("../fonts/sfuidisplay-bold.woff2") format("woff2"), url("../fonts/sfuidisplay-bold.woff") format("woff");
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: #A5AFBA;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

* {
  font-family: 'SFDisplay', Arial, sans-serif;
}

body {
  min-width: 320px;
  color: #4A4A4A;
  font-family: 'SFDisplay', Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 700px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.title-lg {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.1875;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 24px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 12px 40px;
  min-width: 320px;
}

@media only screen and (max-width: 1023px) {
  .header {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__search {
  display: none;
  float: right;
  margin-right: 20px;
  border: 1px solid #E7E7E7;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background: none;
  text-align: center;
  font-size: 0;
  transition: border-color 0.3s, background 0.3s;
}

.header__search:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .header__search:hover {
  border-color: #007AFF;
  background: #007AFF;
}

.no-touch .header__search:hover .header__search-icon {
  fill: #fff;
}

@media only screen and (max-width: 767px) {
  .header__search {
    display: block;
  }
}

.header__search-icon {
  display: inline-block;
  vertical-align: middle;
  fill: #a5afba;
  font-size: 14px;
  transition: fill 0.3s;
}

.profile {
  float: right;
  color: #007AFF;
  font-size: 0;
  white-space: nowrap;
}

.no-touch .profile:hover .profile__title {
  text-decoration: underline;
}

.profile__img, .profile__title {
  display: inline-block;
  vertical-align: middle;
}

.profile__img {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.profile__title {
  padding: 8px 0 8px 14px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

@media only screen and (max-width: 767px) {
  .profile__title {
    display: none;
  }
}

.logo {
  float: left;
  padding: 7px 0 6px;
  color: #4A4A4A;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.1875;
}

@media only screen and (max-width: 767px) {
  .logo {
    padding: 8px 0;
    max-width: 170px;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.logo__in {
  color: inherit;
  transition: color 0.25s;
}

.no-touch .logo__in:hover {
  color: #007AFF;
}

.search {
  float: right;
  padding: 2px 20px 0 0;
  width: 100%;
  max-width: 280px;
}

@media only screen and (max-width: 767px) {
  .search {
    float: none;
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    padding: 12px 60px 12px 20px;
    border-bottom: 1px solid #E7E7E7;
    width: auto;
    max-width: none;
    background: #fff;
    transform: translate3d(0, -100%, 0);
    transition: transform 0.3s;
  }
}

@media only screen and (max-width: 767px) {
  .search.is-open {
    transform: translate3d(0, 0, 0);
  }
}

.search__form {
  position: relative;
}

.search__input {
  padding-left: 28px;
  border: 1px solid #E7E7E7;
  border-radius: 4px;
  width: 100%;
  height: 30px;
  color: #4A4A4A;
  background: none;
  font-size: 15px;
  font-weight: 400;
  line-height: normal;
}

.search__input::placeholder {
  color: #a5afba;
}

.search__btn {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 22px;
  background: none;
  text-align: center;
  font-size: 0;
}

.search__btn:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.search__icon {
  display: inline-block;
  vertical-align: middle;
  fill: #a5afba;
  font-size: 14px;
}

.search__close {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 60px;
  text-align: center;
  font-size: 0;
}

.search__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .search__close {
    display: block;
  }
}

.topic {
  padding: 107px 0 0;
  background: #f9fbff;
}

@media only screen and (max-width: 767px) {
  .topic {
    padding-top: 80px;
  }
}

.topic__main {
  padding-bottom: 15px;
}

.topic__main:after {
  content: " ";
  display: table;
  clear: both;
}

.topic__heading {
  position: relative;
  float: left;
  margin-bottom: -2px;
  width: calc(100% - 200px);
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .topic__heading {
    float: none;
    margin: 0;
    width: 100%;
  }
}

.topic__id {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 100%;
  padding-bottom: 5px;
  color: #a5afba;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media only screen and (max-width: 767px) {
  .topic__id {
    position: static;
  }
}

.topic__title, .topic__meta {
  display: inline-block;
  vertical-align: bottom;
}

.topic__title {
  margin-right: 10px;
}

@media only screen and (max-width: 767px) {
  .topic__title {
    padding-bottom: 2px;
  }
}

.topic__meta {
  padding-bottom: 5px;
  color: #a5afba;
  font-size: 12px;
  line-height: 14px;
}

.topic__btn {
  float: right;
}

@media only screen and (max-width: 767px) {
  .topic__btn {
    margin: 20px 0 0;
    float: none;
  }
}

.topic__menu {
  padding-top: 20px;
}

.topic__list {
  margin: 0 -15px;
}

.topic__item, .topic__control {
  display: inline-block;
  vertical-align: bottom;
}

.topic__item {
  padding: 0 15px;
}

.topic__control {
  position: relative;
  padding-bottom: 10px;
  color: rgba(74, 74, 74, 0.5);
  font-size: 16px;
  line-height: 1.1875;
  cursor: pointer;
  transition: color 0.3s;
}

.no-touch .topic__control:hover {
  color: #007AFF;
}

.topic__control.is-active {
  color: #4A4A4A;
  font-weight: 600;
}

.topic__control.is-active:before {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.topic__control:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 5px solid #007AFF;
  opacity: 0;
  transform: translate3d(0, 10px, 0);
  transition: opacity 0.3s, transform 0.3s;
}

.table {
  font-size: 0;
}

@media only screen and (max-width: 567px) {
  .table__thead {
    display: none;
  }
}

.table__tr {
  position: relative;
  padding-right: 35px;
  border-bottom: 1px solid #f2f2f2;
}

@media only screen and (max-width: 567px) {
  .table__tr {
    padding: 10px 35px 10px 0;
  }
}

.table__th, .table__td {
  display: inline-block;
  vertical-align: middle;
  padding: 0 5px;
}

@media only screen and (max-width: 567px) {
  .table__th, .table__td {
    padding: 0;
  }
}

.table__th[data-column="name"], .table__td[data-column="name"] {
  width: 48%;
}

@media only screen and (max-width: 567px) {
  .table__th[data-column="name"], .table__td[data-column="name"] {
    width: 100%;
  }
}

.table__th[data-column="created"], .table__td[data-column="created"] {
  width: 25.2%;
}

@media only screen and (max-width: 567px) {
  .table__th[data-column="created"], .table__td[data-column="created"] {
    width: 100%;
  }
}

.table__th[data-column="modified"], .table__td[data-column="modified"] {
  width: 25.8%;
}

@media only screen and (max-width: 567px) {
  .table__th[data-column="modified"], .table__td[data-column="modified"] {
    width: 100%;
  }
}

.table__td {
  padding-top: 19px;
  padding-bottom: 19px;
}

@media only screen and (max-width: 567px) {
  .table__td {
    padding-top: 6px;
    padding-bottom: 6px;
  }
}

@media only screen and (max-width: 567px) {
  .table__td[data-title]:before {
    content: attr(data-title);
    display: block;
    margin-bottom: 6px;
    color: #a5afba;
    font-size: 12px;
    line-height: 1;
  }
}

.table__title {
  color: #007AFF;
  font-size: 16px;
  line-height: 1;
}

.table__meta {
  font-size: 14px;
  line-height: 1;
}

.table__th {
  padding-bottom: 3px;
  color: #a5afba;
  font-size: 12px;
  line-height: 1;
}

.table__btn {
  position: absolute;
  right: -14px;
  top: 50%;
  transform: translateY(-50%);
}

@media only screen and (max-width: 567px) {
  .table__btn {
    top: 0;
    transform: translate3d(0, 0, 0);
  }
}

.btn-option {
  padding: 20px 20px 20px 20px;
  background-color: transparent;
  font-size: 0;
}

.btn-option span {
  position: relative;
  display: inline-block;
  background-color: #007AFF;
}

.btn-option span:before, .btn-option span:after {
  content: "";
  position: absolute;
  top: 0;
  background-color: #007AFF;
}

.btn-option span:before {
  left: -6px;
}

.btn-option span:after {
  right: -6px;
}

.btn-option span,
.btn-option span:before,
.btn-option span:after {
  width: 3px;
  height: 3px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 9px 19px 6px;
  border: 1px solid #1e88e5;
  border-radius: 4px;
  color: #1e88e5;
  background: none;
  font-size: 11px;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0.6px;
  text-align: center;
  text-transform: uppercase;
  transition: color 0.25s, background 0.25s;
}

.no-touch .btn:hover {
  color: #fff;
  background: #007AFF;
}
